import React from 'react';

import { Layout as LayoutAntd, Menu, Breadcrumb } from 'antd';

const { Header, Footer, Sider, Content } = LayoutAntd;

function Layout(props) {
  return (
    <LayoutAntd>
      <Header style={{ position: 'fixed', zIndex: 1, width: '100%' }}>
        <div style={{ color: '#FFF', fontWeight: 'bold' }}>
          ĐỒ ÁN TỐT NGHIỆP - HUỲNH GIA BÔI
        </div>
      </Header>
      <Content className="site-layout" style={{ marginTop: 64 }}>
        <div
          className="site-layout-background"
          style={{ padding: 24, minHeight: 380 }}
        >
          {props.children}
        </div>
      </Content>
      <Footer style={{ textAlign: 'center' }}>
        XÂY DỰNG MẠNG RIÊNG ẢO (VPN) VÀ HỆ THỐNG FIREWALL TRÊN RASPBERRY PI
        <br />
        Ứng dụng điều khiển SmartHome Realtime
      </Footer>
    </LayoutAntd>
  );
}

export default Layout;
