import React from 'react';

import { Spin, Space } from 'antd';

function Loading(props) {
  const { visible = false } = props;
  if (!visible) return null;
  return (
    <div
      style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        width: '100%',
        height: '100%',
        backgroundColor: '#FFF',
        position: 'fixed',
        top: 0,
        left: 0,
        zIndex: 100000,
        opacity: 0.5,
      }}
      //className="flex justify-center items-center w-full h-full bg-white fixed top-0 left-0 z-[100000] opacity-50"
    >
      <Space size="large">
        <Spin size="large" />
      </Space>
    </div>
  );
}

export default Loading;
