import React, { useState, useEffect } from 'react';
import Layout from './Layout';
import { Form, Input, Button, Checkbox } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import Loading from './Loading';
import { useHistory } from 'react-router-dom';
import { apiLogin } from './../apis/login';
import decodeToken from '../utils/decodeToken';

function Login() {
  const history = useHistory();

  const [loading, setLoading] = useState(false);
  const [checkLogin, setCheckLogin] = useState(false);

  useEffect(() => {
    async function didMount() {
      const token = localStorage.getItem('token');
      if (decodeToken(token)) {
        history.push('/home');
      }
      setCheckLogin(true);
    }
    didMount();
  }, []);

  async function handleSubmit(e) {
    setLoading(true);
    const { data, status, response } = await apiLogin(e);
    if (!data && response) {
      alert(
        'ERROR | Login failed, please check the entered fields or may have wrong account information'
      );
    } else if (data && (status >= 200 || status < 300)) {
      localStorage.setItem('token', data.result.token);
      history.push('/');
    }
    setLoading(false);
  }

  if (!checkLogin) return <Loading visible={true} />;

  return (
    <Layout>
      <Loading visible={loading} />
      <div
        style={{
          width: '100%',
          height: '100%',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <Form
          name="normal_login"
          className="login-form"
          initialValues={{ remember: true }}
          onFinish={handleSubmit}
        >
          <Form.Item
            name="username"
            rules={[{ required: true, message: 'Please input your Username!' }]}
          >
            <Input
              prefix={<UserOutlined className="site-form-item-icon" />}
              placeholder="Username"
            />
          </Form.Item>
          <Form.Item
            name="password"
            rules={[{ required: true, message: 'Please input your Password!' }]}
          >
            <Input
              prefix={<LockOutlined className="site-form-item-icon" />}
              type="password"
              placeholder="Password"
            />
          </Form.Item>

          <Form.Item>
            <Button
              type="primary"
              htmlType="submit"
              className="login-form-button"
            >
              Log in
            </Button>
          </Form.Item>
        </Form>
      </div>
    </Layout>
  );
}

export default Login;
