import api, { END_POINT } from './api';

async function apiLogin(req) {
  try {
    const res = await api.POST(`${END_POINT}/user/login`, req);
    return res;
  } catch (err) {
    return err;
  }
}

async function apiChangePassword(req) {
  try {
    const res = await api.PUT(`${END_POINT}/user/password`, req);
    return res;
  } catch (err) {
    return err;
  }
}

export { apiLogin, apiChangePassword };
