import React, { useEffect, useState } from 'react';

import { io } from 'socket.io-client';
import { Space, Row, Col, Card, Statistic, Switch, Slider } from 'antd';
import {
  BulbOutlined,
  DesktopOutlined,
  ControlOutlined,
} from '@ant-design/icons';

import Layout from './Layout';

const socket = io(
  process.env.NODE_ENV === 'production' && process.env.END_POINT
    ? process.env.END_POINT
    : '/',
  { transports: ['websocket'] }
);

const { Meta } = Card;

function Home() {
  const [nhietdo, setNhietdo] = useState(0);
  const [den, setDen] = useState('0');
  const [tivi, setTivi] = useState('0');
  const [dieuhoa, setDieuhoa] = useState('0');

  useEffect(() => {
    //Nhiet do
    socket.emit('get_nhietdo');
    socket.on('res_nhietdo', (data) => {
      setNhietdo(data.nhietdo);
      console.log(data);
    });

    //Den
    socket.emit('get_den');
    socket.on('res_den', (data) => {
      setDen(data.den);
      console.log(data);
    });

    //Ten
    socket.emit('get_tivi');
    socket.on('res_tivi', (data) => {
      setTivi(data.tivi);
      console.log(data);
    });

    //Dieuhoa
    socket.emit('get_dieuhoa');
    socket.on('res_dieuhoa', (data) => {
      setDieuhoa(data.dieuhoa);
      console.log(data);
    });
  }, []);

  const updateNhietDoHandle = (value) => {
    socket.emit('update_nhietdo', { nhietdo: value });
    setNhietdo(value);
  };

  const updateDenHandle = (checked) => {
    socket.emit('update_den', { den: checked ? '1' : '0' });
    setDen(checked ? '1' : '0');
  };

  const updateTiviHandle = (checked) => {
    socket.emit('update_tivi', { tivi: checked ? '1' : '0' });
    setTivi(checked ? '1' : '0');
  };

  const updateDieuhoaHandle = (checked) => {
    socket.emit('update_dieuhoa', { dieuhoa: checked ? '1' : '0' });
    setDieuhoa(checked ? '1' : '0');
  };

  return (
    <Layout>
      <Space
        style={{
          display: 'flex',
          flexDirection: 'column',
          flexWrap: 'wrap',
          justifyContent: 'center',
        }}
      >
        <Card title="Thông số môi trường" bordered={false}>
          <div style={{ display: 'flex', flexWrap: 'wrap' }}>
            <Statistic
              title="Nhiệt độ"
              value={nhietdo}
              valueStyle={{ color: '#3f8600' }}
              style={{ display: 'flex', flexDirection: 'column', flex: 1 }}
              suffix="°​C"
            />
            <Statistic
              title="Độ ẩm"
              value={11.28}
              style={{ display: 'flex', flexDirection: 'column', flex: 1 }}
              valueStyle={{ color: '#3f8600' }}
              suffix="%"
            />
          </div>
        </Card>

        <Card title="Điều khiển thiết bị" bordered={false} id="control">
          <div style={{ display: 'flex', flexWrap: 'wrap' }}>
            <div style={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
              <Statistic title="Đèn" prefix={<BulbOutlined />} />
              <Switch
                style={{ width: 50 }}
                checked={den == '0' ? false : true}
                onChange={updateDenHandle}
              />
            </div>
            <div style={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
              <Statistic title="Tivi" prefix={<DesktopOutlined />} />
              <Switch
                style={{ width: 50 }}
                checked={tivi == '0' ? false : true}
                onChange={updateTiviHandle}
              />
            </div>

            <div style={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
              <Statistic title="Điều hoà" prefix={<ControlOutlined />} />
              <Switch
                style={{ width: 50 }}
                checked={dieuhoa == '0' ? false : true}
                onChange={updateDieuhoaHandle}
              />
            </div>
          </div>
        </Card>

        <Card title="Nhiệt độ điều hoà" bordered={false} id="control">
          <div style={{ display: 'flex', flexWrap: 'wrap' }}>
            <Slider
              defaultValue={nhietdo}
              min={16}
              max={35}
              style={{ width: '100%' }}
              value={nhietdo}
              onChange={updateNhietDoHandle}
            />
          </div>
        </Card>
      </Space>
    </Layout>
  );
}

export default Home;
